body {
  font-family: Arial,sans-serif;
  color: #5c5c5c;
  margin: 0;

  &.springster {
    font-family: Helvetica,Arial,sans-serif;
    background-color: #7300ff;
    color: #fff;
  }

  &.malawi {
    color: #412a83;
  }
}

hr {
  margin: 2em 0;
}

.text-container {
}

.text-body {
  padding: 0 1em;

  @media screen and (min-width: 500px) {
      width: 500px;
      margin: 0 auto;
  }
}

.branding {
  padding: 1em 0;
  background-color: #00b8ae;

  .springster & {
    background-color: inherit;
  }

  .malawi & {
    background-color: #ffed00;
  }
}

.brand-logo {
  display: block;
  margin: 0 auto;
  max-width: 200px;
}

.brand-icon {
  display: block;
  margin: 2em auto;
  max-width: 60px;
  
  .springster & {
    margin-top: 0;
  }
}

.footer{
  text-align: center;
  font-size: 0.8em;
  margin-bottom: 2em; 
}